.env-dropdown {
  display: flex;
  align-items: center;
  position: fixed;
  top: 28px;
  right: 28px;
}

/* Style the label for the dropdown */
.env-dropdown label {
  margin-right: 8px;
  color: #ffffff; /* 白色字体 */
}

/* Style the select dropdown */
.env-dropdown select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  padding: 8px;
  font-size: 12px;
  border: 1px solid #333333; /* 深灰边框 */
  border-radius: 5px;
  cursor: pointer;
  background-color: #404040; /* 深灰背景 */
  color: #ffffff; /* 白色字体 */
  text-align: center; /* 文字居中 */
  outline: none; /* 去掉选中时的边框 */

}

/* Custom arrow for the dropdown */
.env-dropdown select::after {
  content: '\25BC'; /* Unicode编码，表示向下的三角形 */
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: #ffffff; /* 白色箭头 */
}

/* Hover effect for better visual feedback */
.env-dropdown select:hover {
  background-color: #555555; /* 深一点的灰色 */
}

/* 样式下拉菜单容器 */
.animation-dropdown {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 28px; /* 根据需要调整距离底部的距离 */
  width: 100%;
}

/* 样式下拉菜单 */
.animation-dropdown select {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  padding: 8px;
  font-size: 12px;
  border: 1px solid #333333;
  border-radius: 5px;
  cursor: pointer;
  background-color: #404040; /* 调整背景颜色以匹配 env-dropdown */
  color: #ffffff; /* 调整文本颜色以匹配 env-dropdown */
  text-align: center;
  outline: none;
  position: relative;
  z-index: 1;
}

/* 伪类模拟上方容器 */
.animation-dropdown select:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

/* 下拉菜单选项往上展开 */
.animation-dropdown select option {
  transform: translateY(-100%);
}

/* 自定义下拉菜单的箭头 */
.animation-dropdown select::after {
  content: '\25B2'; /* 这里改为向上的箭头 */
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: #ffffff; /* 调整箭头颜色以匹配 env-dropdown */
}

/* 悬停效果以提供更好的视觉反馈 */
.animation-dropdown select:hover {
  background-color: #555555; /* 调整悬停时的背景颜色以匹配 env-dropdown */
}

